import React, { useState } from "react";

import dayjs from "dayjs";
import { Button, Input, Select } from "antd";
import { useInterval, useMount, useTitle, useUnmount } from "let-hooks/lib";
import styles from "./style.module.scss";
import Head from "next/head";

const CurrentTime = () => {
  const [time, setTime] = useState<dayjs.Dayjs>();

  const intveral = useInterval(
    () => {
      setTime(dayjs());
    },
    {
      delay: 500,
    }
  );

  useMount(() => {
    intveral.run();
  });

  useUnmount(() => {
    intveral?.clear();
  });

  return (
    <div className={styles.current}>
      当前时间：
      <span>{time?.format("YYYY-MM-DD HH:mm:ss")}</span>
      <span> ， 时间戳：</span>
      <span>{time?.valueOf()}</span>
      <Button size="small" onClick={intveral.run}>
        开始
      </Button>
      <Button size="small" onClick={intveral.clear} danger>
        暂停
      </Button>
    </div>
  );
};

// 时间戳转日期
const TimeToTimestamp = () => {
  const [timestamp, setTimestamp] = useState<string>();

  const handleTimestampChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value?.trim() || "";
    setTimestamp(value);
  };
  return (
    <div className={styles.row}>
      <span>时间戳</span>
      <span className={styles.input_wrapper}>
        <Input value={timestamp} onChange={handleTimestampChange} />
      </span>
      毫秒
      <span className={styles.input_wrapper}>
        <Input
          readOnly
          value={
            timestamp
              ? dayjs(Number(timestamp)).format("YYYY-MM-DD HH:mm:ss")
              : undefined
          }
        />
      </span>
    </div>
  );
};

/* 日期转时间戳 */
const TimestampToTime = () => {
  const [datetime, setDatetime] = useState<string>();
  const [convertType, setConvertType] = useState<number>(1);

  const formatFunc = () => {
    if (!datetime) {
      return;
    }
    const milliSec = dayjs(datetime).valueOf();
    if (convertType === 1) {
      return Math.floor(milliSec / 1000);
    }

    return milliSec;
  };

  return (
    <div className={styles.row}>
      <span>时间</span>
      <span className={styles.input_wrapper}>
        <Input
          value={datetime}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            setDatetime(e.target.value?.trim() || "")
          }
        />
      </span>
      时间戳
      <span className={styles.input_wrapper}>
        <Input readOnly value={formatFunc()} />
      </span>
      <Select
        value={convertType}
        options={[
          { label: "秒", value: 1 },
          { label: "毫秒", value: 2 },
        ]}
        onChange={setConvertType}
      ></Select>
    </div>
  );
};

const DateTimePage = () => {
  useTitle("时间戳转换");
  return (
    <div className={styles.container}>
      <Head>
        <meta name="keywords" content="时间戳,timestamp" />
      </Head>
      <div className={styles.main}>
        <h2>时间转换</h2>
        <CurrentTime />
        <TimeToTimestamp />
        <TimestampToTime />
      </div>
    </div>
  );
};

export default DateTimePage;
